.main {
  display: block;
  height: 100vh;
  width: 100vw;
  background-image: url(./../../images/background-image.jpg);
  background-size: cover;
  background-blend-mode: hue;
  background-position: center;
  background-repeat: repeat-x;
  position: fixed;
  transition: all 500ms ease-in-out;
}

.header {
  padding: 10px;
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 15%;
  width: 100%;
}

.header_theme_switch {
  margin-top: 0px;
}

.header_lang_options {
  width: 50px;
  margin-left: 75%;
  margin-right: 30px;
  color: $orange;
}

.container {
  display: flex;
  height: 75%;
  width: 100%;
}

.navbar {
  display: block;
  float: left;
  align-items: center;
  align-self: center;
  height: auto;
  width: 15%;
  padding: auto;
}

.body {
  width: 80%;
}

.body_title {
  display: block;
  font-size: 3vw;
  margin-top: 10px;
  margin-bottom: 25px;
  letter-spacing: 1px;
  font-stretch: ultra-expanded;
  font-family: "Thasadith", sans-serif;
  & i {
    font-size: 28px;
    margin-right: 20px;
  }
  .body_title_point {
    color: $orange;
  }
}

.body_paragraph {
  font-size: 1.3vw;
  width: 90%;
  letter-spacing: 1px;
  margin-bottom: 20px;
}

.body_paragraph i {
  color: $orange;
  //font-size: 1.5vw;
  text-decoration: none !important;
}

.body_paragraph_p {
  margin-bottom: 20px;
}

.body_paragraph_span {
  color: $orange;
  &:hover{
    font-weight: 600;
  }
}

.media_icon {
  width: 100px;
  cursor: pointer;
  margin: 0 auto;
  margin-bottom: 25px;
  transition: color 0.2s ease;
  &:hover {
    color: $orange;
  }
}

.media_icon:hover .overlay {
  opacity: 1;
  transform: translateX(0%);
}

.overlay {
  height: 20px;
  text-align: left;
  margin-left: 10px;
  position: absolute;
  width: 60px;
  letter-spacing: 1px;
  padding: 5px;
  cursor: pointer;
  opacity: 0;
  transform: translateX(-30%);
  transition: all 400ms;
}

.footer {
  clear: both;
  text-align: center;
  font-size: 10px;
  letter-spacing: 1px;
  padding: 10px;
  height: 15%;
}

.social_media_icons {
  display: flex;
  flex-direction: column;
}


@media screen and (max-width: 500px) {
  .body_paragraph {
    font-size: 4vw;
  }

  .body_title {
    font-size: 6.5vw;
  }

  .media_icon {
    width: 50px;
  }

  .header_lang_options {
    margin-left: 60%;
    margin-right: 30px;
  }
  .navbar{
    width: 20%;
  }
}